const baseUrl = process.env.REACT_APP_API_URL;

// eslint-disable-next-line @typescript-eslint/no-redeclare
export function tokenLessFetch(endpoint: string, data?: Object, method: string = "GET"): Promise<Response> {
  const url = `${baseUrl}/${endpoint}`;
  if (method === "GET") {
    return fetch(url);
  } else {
    return fetch(url, {
      method,
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify(data),
    });
  }
}

export function fetchWithToken(endpoint: string, data?: Object, method: string = "GET"): Promise<Response> {
  const url = `${baseUrl}/${endpoint}`;
  const token = localStorage.getItem("token") || "";
  if (method === "GET") {
    return fetch(url, {
      method,
      headers: {
        "x-token": token,
      },
    });
  } else {
    return fetch(url, {
      method,
      headers: {
        "Content-type": "application/json",
        "x-token": token,
      },
      body: JSON.stringify(data),
    });
  }
}
